@import url(https://stackpath.bootstrapcdn.com/bootstrap/4.3.1/css/bootstrap.min.css);
@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
.loginpage{position:relative; min-height:100vh;
	/*background-image:url('../images/login.jpg');	*/
	background-size:cover;
	background-repeat:no-repeat;
	background-position:top center
}
.reset{
	/*background-image:url('../images/bg1.jpg');*/
}
.login-form {
	width:100%;
    max-width: 300px;
	padding:25px 20px !important;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
	background: rgba(0,0,0,0.4);
    box-shadow: 0px 0px 4px -3px #000;
    border-radius: 10px;
}
.login-form input.ant-input{box-shadow: 0 0 0 20px #fff inset !important;}
.login-form a{
	color:#fff
}
.login-form-forgot {
  float: right;
}
.login-form-button {
  width: 100%;
}
.loginpage span{color:#fff}
.loginpage .ant-alert-description{color:#000}
.loginpage .ant-alert-success .ant-alert-message{color:#52c41a}
.loginpage .ant-alert-error .ant-alert-message{color:red}
.login-form-forgot{cursor:pointer; line-height: 25px;}

@media screen and (max-height:350px){
	.login-form{position:relative; top:0; -webkit-transform: translate(-50%,0); transform: translate(-50%,0);}
}
.error{color:red}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}




:root {
  --main-bg-color: coral;
  --primary: #1abc9c;
  --primary-dark: #16a085;
  --secondary: #16a085;
  --gray-light: #ecf0f1;
  --silver:#bdc3c7;
  --silver-dark:#92979a;
}

*, *:focus{box-sizing: border-box; outline: none !important; border-radius: 0 !important}
body{ background: #ecf0f1; background: var(--gray-light) }

html, body, :global(#root) {
  height: 100%;
}




.btn-primary{background: #1abc9c;background: var(--primary); border-color:#1abc9c; border-color:var(--primary)}
.btn-primary:hover, .btn-primary:focus{background: #16a085;background: var(--primary-dark); border-color:#16a085; border-color:var(--primary-dark)}
.bg-primary{background-color: #1abc9c !important;background-color: var(--primary) !important;}
.text-primary{color: #1abc9c !important;color: var(--primary) !important;}

.btn-silver{background: #bdc3c7;background: var(--silver); border-color:#bdc3c7; border-color:var(--silver); color:#fff}
.btn-silver:hover, .btn-silver:focus{background: #92979a;background: var(--silver-dark); border-color:#92979a; border-color:var(--silver-dark); color:#fff}
.bg-silver{background-color: #bdc3c7 !important;background-color: var(--silver) !important;}



