@import 'https://stackpath.bootstrapcdn.com/bootstrap/4.3.1/css/bootstrap.min.css';
@import 'https://fonts.googleapis.com/icon?family=Material+Icons';

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}




:root {
  --main-bg-color: coral;
  --primary: #1abc9c;
  --primary-dark: #16a085;
  --secondary: #16a085;
  --gray-light: #ecf0f1;
  --silver:#bdc3c7;
  --silver-dark:#92979a;
}

*, *:focus{box-sizing: border-box; outline: none !important; border-radius: 0 !important}
body{ background: var(--gray-light) }

html, body, :global(#root) {
  height: 100%;
}




.btn-primary{background: var(--primary); border-color:var(--primary)}
.btn-primary:hover, .btn-primary:focus{background: var(--primary-dark); border-color:var(--primary-dark)}
.bg-primary{background-color: var(--primary) !important;}
.text-primary{color: var(--primary) !important;}

.btn-silver{background: var(--silver); border-color:var(--silver); color:#fff}
.btn-silver:hover, .btn-silver:focus{background: var(--silver-dark); border-color:var(--silver-dark); color:#fff}
.bg-silver{background-color: var(--silver) !important;}


