.loginpage{position:relative; min-height:100vh;
	/*background-image:url('../images/login.jpg');	*/
	background-size:cover;
	background-repeat:no-repeat;
	background-position:top center
}
.reset{
	/*background-image:url('../images/bg1.jpg');*/
}
.login-form {
	width:100%;
    max-width: 300px;
	padding:25px 20px !important;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
	background: rgba(0,0,0,0.4);
    box-shadow: 0px 0px 4px -3px #000;
    border-radius: 10px;
}
.login-form input.ant-input{box-shadow: 0 0 0 20px #fff inset !important;}
.login-form a{
	color:#fff
}
.login-form-forgot {
  float: right;
}
.login-form-button {
  width: 100%;
}
.loginpage span{color:#fff}
.loginpage .ant-alert-description{color:#000}
.loginpage .ant-alert-success .ant-alert-message{color:#52c41a}
.loginpage .ant-alert-error .ant-alert-message{color:red}
.login-form-forgot{cursor:pointer; line-height: 25px;}

@media screen and (max-height:350px){
	.login-form{position:relative; top:0; transform: translate(-50%,0);}
}
.error{color:red}
